.page {
  font-family: Cinzel, serif;
  font-size: 16px;

  color: $main-text-color;

  line-height: 27px;

  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
    overflow-x: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;

    opacity: 0;

    transition: all 0.3s;
    transform: translateX(-200%);

    pointer-events: none;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
    }
  }
}
